@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&display=swap');
body {
    font-family: 'Open Sans', sans-serif;
    height: 100vh;
        background-size: 700px auto;
    background-position: calc(50% - 350px) center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.container {
    background: #fff;
    width: 60%;
    margin: auto;
    height: 100%;
}
.auth-wrapper {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    box-shadow: 0px 10px 50px 0px #00000014;
    padding: 15px 30px;
    height: 490px;
}
.auth-wrapper img {
    width: 215px;
}
.floating {
    position: relative;
    height: 55px;
}
label {
    font-weight: 600;
}
.floating label {
    color: #000a2e;
    font-size: 14px;
    z-index: 0;
    position: absolute;
    top: 15px;
    left: 10px;
    transform: translate3d(0, 0, 0);
    transition: all 0.2s ease-in-out;
}
.form-control {
    border: 1px solid #d8dbe4;
    border-radius: 0;
    border-bottom: 0;
    padding: 8px 12px;
    padding-top: 30px;
    font-size: 15px;
    height: 55px;
    color: #000;
    font-weight: bold;
}
.floating input:focus {
    padding-top: 20px;
    box-shadow: none;
}
.floating input:focus + label {
    font-size: 12px;
    color: rgba(0, 10, 46, 0.8);
    transform: translate3d(0, -10px, 0);
    top: 12px;
}
.agree {
    font-size: 14px;
    color: #000;
}
.agree a {
    color: #4786ff;
    font-weight: bold;
}
.backtologin {
    color: #979ea3;
    font-size: 14px;
    font-weight: 600;
}
.backtologin:hover {
    color: #82898f;
    text-decoration: none;
}
.btn.btn-primary {
    border: none;
    border-radius: 3px;
    box-shadow: 0px 5px 15px 0px #00000026;
    background-color: #4786ff;
    font-size: 13px;
    font-weight: 700;
    padding: 10px 20px;
    text-transform: uppercase;
}
.btn.btn-primary:hover {
    background: #fe8114;
}
.btn.btn-primary.btn-create {
    background: #262626;
    box-shadow: 0px 5px 15px 0px #26262652;
    width: 100%;
}
.divider hr {
    border: 1px dashed #e0e2e5;
}
.divider p {
    margin: 0px 11px;
    color: #78828a;
    font-size: 14px;
    background: #fff;
    display: inline-block;
    padding: 0 10px;
    position: relative;
    top: -30px;
}


@media screen and (max-width: 768px) {
    .auth-wrapper {
        height: auto;
    }
}


@media screen and (max-width: 991px) {
    .container {
        width: 90%;
        max-width: 90%;
    }
}

.auth-wrapper img {
  width: 136px;
}


.floating label {
    color: #000a2e;
    font-size: 14px;
    z-index: 0;
    position: absolute;
    top: 9px;
    left: 10px;
    transform: translate3d(0, 0, 0);
    transition: all 0.2s ease-in-out;
}


.auth-wrapper img {
    width: 190px;
}
.agree {
    font-size: 14px;
    color: #000;
    white-space: nowrap;
}