
.btn.btn-primary.btn-support {
    padding: 3px 10px;
    font-size: 12px;
    display: flex;
    align-items: center;
    background: rgb(219 73 49);
    border: 1px solid rgb(219 73 49);
}
.foot p {
    color: rgb(133, 137, 144);
    font-size: 13px;
    text-transform: uppercase;
    margin-bottom: 0;
}
.footer-right svg {
    color: rgb(133, 137, 144);
    width: 1.2em;
    height: 1.2em;
    margin-top: -4px;
}
.footer-right span,
.footer-right svg:hover {
    color: #fff;
}
.footer-right .fullscreen {
    width: 1em;
    height: 1em;
}
.tooltip-inner {
    background-color: rgb(80, 88, 110)!important;
}


