.dash-navbar {
	padding-top: 0;
	padding-bottom: 0;
	padding-left: 15px !important;
	padding-right: 15px !important;
	background-color: #181f2c;
	display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
	display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
	display: -ms-flexbox;      /* TWEENER - IE 10 */
	display: -webkit-flex;     /* NEW - Chrome */
	display: flex;             /* NEW, Spec - Opera 12.1, Firefox 20+ */
	-webkit-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-box-align: center;
	-moz-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;
	position: fixed;
	top: 0;
	z-index: 10;
	width: 100%;
	height: 70px;
	font-size: 90%;
}

.dash-navbar .brand {
	width: 200px;
}

.dash-navbar .brand img {
	max-width: 150px;
	width: 100%;
	display: block;
}

.dash-navbar .stock {
	width: 700px;
}

.dash-navbar .stock .stock-box {
	background-color: transparent;
	display: inline-block;
	padding: 10px 75px 20px 10px;
	height: 50px;
	position: relative;
	border-radius: 5px;
	margin-right: 10px;
	font-size: 85%;
	cursor: pointer;
	border: 1px solid #555;
}

.dash-navbar .stock .stock-box span.close {
	position: absolute;
	top: -5px;
	right: -5px;
	padding: 10px 10px;
	cursor: pointer;
	font-size: 20px;
}

.dash-navbar .stock .stock-box span.close svg {
	fill: #555;
	width: 20px;
	height: 20px;
	display: block;
}

.dash-navbar .stock .stock-box span.close svg:hover {
	fill: #ddd;
}


.dash-navbar .stock .stock-box span {
	display: block !important;
}

.dash-navbar .stock .stock-box span.name {
	font-size: 85%;
	margin-bottom: 5px;
	display: block !important;
}

.dash-navbar .stock .stock-box span.currency {
	font-weight: bold;
	font-size: 87%;
}

.dash-navbar .stock .search {
	width: 50px;
	height: 50px;
	background-color: transparent;
	border: 1px solid #555;
	border-radius: 5px;
	position: relative;
	cursor: pointer;
}

.dash-navbar .stock .search:hover {
	background-color: #232631;
}

.dash-navbar .stock .search svg {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.dash-navbar .account {
	width: -webkit-calc(100% - 900px);
	width: calc(100% - 900px);
}

.dash-navbar .account .account-box {
	justify-content: flex-end;
}

.dash-navbar .account .verify a {
	background-color: #28b756;
	color: #fff;
	text-decoration: none;
	outline: none;
	padding: 15px 15px;
	margin-right: 20px;
}

.dash-navbar .account .verify a:hover {
	background-color: #1f7a2d;
}

.dash-navbar .account .real-account a {
	color: #28b756;
	text-decoration: none;
	outline: none;
}

.dash-navbar .account .real-account .text {
	text-align: right;
}

.dash-navbar .account .real-account .text .dtl {
	text-transform: uppercase;
	font-size: 10px;
	font-weight: normal;
}

.dash-navbar .account .real-account .text .amount {
	margin-top: -5px;
	font-size: 130%;
	font-weight: normal;
}

.dash-navbar .account .real-account .icon {
	margin-left: 10px;
}

.dash-navbar .account .real-account .icon svg {
	fill: #28b756;
	width: 15px;
	height: 15px;
}

.dash-navbar .account .notification {
	position: relative;
	text-decoration: none !important;
	padding: 0;
	margin: 0;
	border: none;
}

.dash-navbar .account .notification a {
	padding: 10px 10px;
	margin-left: 20px;
	display: inline-block;
	text-decoration: none !important;
}

.dash-navbar .account .account-profile .profile {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-color: #fff;
	margin-left: 20px;
}

