.sidebar-wrapper {
    width: 300px;
    display: flex;
    height: 100%;
    background: #20283a;
    flex-direction: column;
    box-shadow: 2px 0px 20px #00000030;
    position: relative;
}
.sidebar-wrapper header {
    height: 40px;
    min-height: 40px;
    max-height: 40px;
    background: #181e2b;
    padding-right: 5px;
    margin: 6px 4px;
    border-radius: 0px;
}
.sidebar-wrapper header .watchlist {
    padding: 10px;
    color: #949fab;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 500;
}
.sidebar-wrapper header .search {
    border: none;
    background: #353c4f;
    border-radius: 2px;
    padding: 5px;
    color: #f4f6f9;
    width: 83px;
    outline: none;
    height: auto;
    font-size: 13px;
}
.sidebar-wrapper nav {
    margin: 0 4px;
}
.sidebar-wrapper #watching-list {
    list-style-type: none;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #33394a;
    padding: 0;
    padding-bottom: 5px;
}
.sidebar-wrapper nav li {
    font-size: 13px;
    color: #a6acb3;
}
.sidebar-wrapper .watchlist-details {
    display: flex;
    height: 30%;
    overflow-x: hidden;
    overflow-y: auto;
    flex-direction: column;
    margin-bottom: 10px;
}
.sidebar-wrapper .details-wrapper {
    background: #262b3e;
}
.infocurrency-wrapper {
    background: #171b29;
}
.infocurrency-wrapper h6 {
    color: #c5cbce;
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 8px;
}
.infocurrency-wrapper .badge-code {
    display: inline-block;
    background: #f14700;
    color: #fff;
    font-size: 13px;
    font-weight: 400;
    padding: 3px 10px;
    border-radius: 40px;
}
.infocurrency-wrapper .price {
    color: #c5cbce;
    font-size: 28px;
    font-weight: 700;
    line-height: 27px;
    margin-right: 10px;
}
.infocurrency-wrapper .price span {
    color: #c5cbce;
    font-size: 15px;
    font-weight: 700;
}
.infocurrency-range {
    margin-top: -10px;
}
.infocurrency-range span {
    font-weight: 700;
    font-size: 14px;
    color: #c5cbce;
}
.infocurrency-range label {
    font-size: 11px;
    color: #758696;
}
.market-wrapper span {
    color: #c5cbce;
    font-size: 13px;
    text-transform: uppercase;
    padding: 6px 0px;
}
.market-wrapper div {
    flex: 1;
    margin: 0px 10px;
    height: 1px;
    background: #4c525e;
    opacity: 0.5;
}
.btn.btn-success.btn-load {
    background: #29c359;
    border: 1px solid #29c359;
    padding: 5px 10px;
    font-size: 13px;
}

