.switch-field input {
	position: absolute !important;
	clip: rect(0, 0, 0, 0);
	height: 1px;
	width: 1px;
	border: 0;
	overflow: hidden;
}

.switch-field label {
	margin: 0;
	background-color: #333333;
	color: #969999;
	font-size: 12px;
