	transition: all 0.1s ease-in-out;
}

.switch-field label:hover {
	cursor: pointer;
}

.switch-field input:checked + label {
	background-color: #f25f5c;
	box-shadow: none;
	color: #fff;
}

.switch-field input:checked + label:first-of-type {
	background-color: #70c1b3;
