.withdraw-modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	max-width: 700px;
	width: 100%;
	background-color: #1c2030;
	height: 480px;
}

.withdraw-modal .header {
	background-color: #262c3b;
	padding: 15px 15px;
	font-size: 14px;
	position: relative;
	z-index: 2;
}

.withdraw-modal .dash-row { 
}

.withdraw-modal .sidebar {
	width: 25%;
	background-color: #1f2333;
	height: 450px;
	position: absolute;
	top: 0;
	left: 0;
	padding-top: 46.5px;
}

.withdraw-modal .sidebar .links a {
	background-color: #222635;
	color: #fff;
	text-decoration: none;
	border-left: 3px solid #29c359;
	position: relative;
	padding: 10px 15px;
	display: inline-block;
	width: 100%;
}

.withdraw-modal .sidebar .links a span {
	display: block;
	margin: 5px 0;
}

.withdraw-modal .content {
	width: 70%;
	height: 480px;
	overflow: auto;
	padding: 15px 15px;
	position: absolute;
	top: 0;
	left: 0;
	padding-top: 46.5px;
	margin-left: 30%;
}

.withdraw-modal .content .range {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 10px 10px;
	text-align: center;
	width: 100%;
	font-stretch: 15px;
}

.withdraw-modal .content .range .input {
	width: calc(100% - 75px);
	position: relative;
}

.withdraw-modal .content .range .output {
	width: 75px;
	position: relative;
}

.withdraw-modal .content .range .input input[type="range"] {
	width: calc(100% - 75px);
	display: block;
	position: relative;
	-webkit-appearance: none;
	vertical-align: middle;
	outline: none;
	border: none;
	padding: 0;
	background: none;
}

.withdraw-modal .content .range .input input[type="range"]::-webkit-slider-runnable-track {
  background-color: #d7dbdd;
  height: 6px;
  border-radius: 3px;
  border: 1px solid transparent;
}

.withdraw-modal .content .range .input input[type="range"][disabled]::-webkit-slider-runnable-track {
  border: 1px solid #d7dbdd;
  background-color: transparent;
  opacity: 0.4;
}

.withdraw-modal .content .range .input input[type="range"]::-moz-range-track {
  background-color: #d7dbdd;
  height: 6px;
  border-radius: 3px;
  border: none;
}

.withdraw-modal .content .range .input input[type="range"]::-ms-track {
  color: transparent;
  border: none;
  background: none;
  height: 6px;
}

.withdraw-modal .content .range .input input[type="range"]::-ms-fill-lower { 
  background-color: #d7dbdd;
  border-radius: 3px;
}

.withdraw-modal .content .range .input input[type="range"]::-ms-fill-upper { 
  background-color: #d7dbdd;
  border-radius: 3px;
}

.withdraw-modal .content .range .input input[type="range"]::-ms-tooltip { display: none; /* display and visibility only */ }

.withdraw-modal .content .range .input input[type="range"]::-moz-range-thumb {
  border-radius: 20px;
  height: 18px;
  width: 18px;
  border: none;
  background: none;
  background-color: #606670;
}

.withdraw-modal .content .range .input input[type="range"]:active::-moz-range-thumb {
  outline: none;
}

.withdraw-modal .content .range .input input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none !important;
  border-radius: 100%;
  background-color: #606670;
  height: 18px;
  width: 18px;
  margin-top: -7px;
}

.withdraw-modal .content .range .input input[type="range"][disabled]::-webkit-slider-thumb {
  background-color: transparent;
  border: 1px solid #d7dbdd;
}

.withdraw-modal .content .range .input input[type="range"]:active::-webkit-slider-thumb {
  outline: none;
}

.withdraw-modal .content .range .input input[type="range"]::-ms-thumb { 
  border-radius: 100%;
  background-color: #606670;
  height: 18px;
  width: 18px; 
  border: none;
}

.withdraw-modal .content .range .input input[type="range"]:active::-ms-thumb {
  border: none;
}

