.switch-field-round {
  background-color: #333333;
  display: inline-block;
  padding: 7px 5px;
  border-radius: 4px;
  position: relative;
}

.switch-field-inline {
  display: inline-block;
  width: 20%;
  margin: 0 25px 25px 25px;
}

.switch-field-inline span {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
  font-size: 15px;
}

.switch-field input {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.switch-field label {
  margin: 0;
  background-color: #333333;
  color: #969999;
  font-size: 12px;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  padding: 5px 12px;
  margin-right: -1px;
  /*border: 1px solid rgba(0, 0, 0, 0.2);*/
  /*box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);*/
  transition: all 0.1s ease-in-out;
}

.switch-field label:hover {
  cursor: pointer;
}

.switch-field input:checked + label {
  background-color: #f25f5c;
  box-shadow: none;
  color: #fff;
}

.switch-field input:checked + label:first-of-type {
  background-color: #70c1b3;
}

.switch-field label:first-of-type {
  border-radius: 4px;
}

.switch-field label:last-of-type {
  border-radius: 4px;
}

