.switch {
  position: relative;
  display: inline-block;
  width: 80px;
  height: 40px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  transition: 0.4s;
  box-shadow: 2px 2px 0 0 #67696b;
}

.slider:before {
  position: absolute;
  content: "";
  height: 30px;
  width: 30px;
  left: 5px;
  bottom: 5px;
  background-color: white;
  transition: 0.4s;
}

.slider:after {
  color: #fff;
  font-size: 14px;
  line-height: 1;
  font-family: "Open Sans", "sans-serif";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  text-translate: uppercase;
  font-weight: bold;
}

input:checked + .slider {
  background-color: black;
  box-shadow: 2px 2px 0 0 #5a8325;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(40px);
}

input:checked + .slider:after {
  right: auto;
  left: 10px;
}

/* /* Rounded sliders */
.slider.round {
  border-radius: 19px;
}

.slider.round:before {
  border-radius: 50%;
}

