.navbar {
    background-color: #1a202d;
}
.navbar-expand .navbar-nav .nav-link {
    padding: 0 5px;
}
.navbar-brand svg {
    width: 124px;
    height: 30px;
}
.navbar-nav.options-nav {
    width: 60%;
}
/* .x-auto {
    overflow-x: auto;
} */
.stock-box {
    padding: 10px 55px 5px 0;
    border-radius: 5px;
    margin-right: 5px;
    cursor: pointer;
    border: 1px solid #555;
    display: flex;
}
.navbar span {
    display: block;
}
.navbar .name {
    color: #fff;
    font-size: 13px;
    font-weight: bold;
}
.navbar .currency {
    color: grey;
    font-size: 10.5px;
}
.navbar-dark .navbar-nav .nav-link:hover .stock-box {
    background: rgb(54 60 77)!important;
}
.navbar-dark .navbar-nav .nav-link.active .stock-box {
    background: rgb(54 60 77)!important;
    border-bottom: 2px solid#f07001!important;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}
.navbar-dark .navbar-nav .nav-link.active svg {
    color: rgba(255,255,255,.5);
}
.navbar-dark .navbar-nav .nav-link.active svg:hover,
.navbar-dark .navbar-nav .nav-link:hover .svg:hover {
    color: rgb(255,255,255);
    font-weight: bold;
}
.icon-wrapper {
    margin-top: -15px;
}
.btn.btn-plus {
    color: rgb(140 143 150);
    font-size: 30px;
    border: 1px solid #555;
    padding: 9px 10px;
    display: flex;
}
.btn.btn-plus:hover,
.features-wrapper a:hover {
    background: rgb(54 60 77);
    color: #fff;
}
.camera-wrapper {
    font-size: 22px;
    background: rgb(54, 60, 77);
    border-radius: 50%;
    height: 50px;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(140 143 150);
}
.camera-wrapper:hover,
.features-wrapper a:hover svg {
    color: #fff;
}
.navbar-expand .navbar-nav .dropdown-toggle.nav-link {
    display: flex;
    align-items: center;
}
.navbar-expand .navbar-nav .balance-wrapper .dropdown-toggle::after {
    border-top: .3em solid #f07001;
}
.account-wrapper {
    color: #f07001;
    text-align: right;
}
.account-wrapper p {
    text-transform: uppercase;
    font-size: 12px;
}
.account-wrapper p:last-child,
.account-wrapper:hover {
    color: rgb(186, 91, 11);
}
.account-wrapper h6 {
    font-size: 25px;
    font-weight: bold;
}
.navbar-expand .navbar-nav .dropdown-menu {
    left: auto;
    margin: 0px;
    min-width: 30rem;
    right: -45px;
    top: 121%;
    padding: 0;
}
.navbar-expand .navbar-nav .dropdown-menu.show {
    display: flex;
}
.profile-wrapper {
    width: 60%;
    background: rgb(39, 45, 59);
    padding: 30px 20px;
}
.profile-wrapper h6 {
    font-size: 18px;
    font-weight: bold;
    color: #fff;
}
.profile-wrapper p {
    color: rgba(255,255,255,.5);
    font-size: 13px;
}
.profile-wrapper span {
    font-size: 10px;
    color: rgba(255,255,255,.5);
}
.date-wrapper p {
    color: #fff;
}
.features-wrapper {
    width: 40%;
    background: rgb(29, 33, 48);
}
.features-wrapper {
    padding: 15px 0;
}
.features-wrapper a {
    color: #fff;
    font-size: 14px;
    display: flex;
    align-items: center;
}
.features-wrapper a svg {
    color: rgba(255, 255, 255, 0.5);
}
.tour-wrapper {
    background: rgb(76 82 104);
    padding: 10px;
}
.tour-wrapper p {
    font-size: 13px;
    color: #fff;
}
.dropdown-divider {
    border: 1px solid #555;
}
.navbar-expand .navbar-nav .balance-wrapper .dropdown-menu {
    min-width: 45rem;
    right: -17px;
    top: 109%;
}
.balance-wrapper .profile-wrapper {
    width: 40%;
}
.balance-wrapper .features-wrapper {
    width: 60%;
}
.features-wrapper h6 {
    font-size: 13px;
    color: #fff;
    font-weight: bold;
}
.features-wrapper p,
.practice-account h6 span {
    color: rgba(255,255,255,.5);
    font-size: 13px;
}
.features-wrapper .amount {
    color: #2cac40;
    font-weight: bold;
}
.btn.btn-secondary.btn-deposit,
.btn.btn-primary.btn-enable {
    padding: 7px 40px;
}   
.btn.btn-secondary.btn-deposit {
    background: rgb(55, 60, 70);
    border: 0;
    text-transform: capitalize;
    font-weight: normal;
    font-size: 12px;
}
.practice-account .btn.btn-secondary.btn-deposit {
    background: rgb(68, 74, 95);
}
.btn.btn-primary.btn-enable {
    background: #f07001;
    border: 1px solid #f07001;
    font-size: 14px;
}
.features-wrapper .real-account:hover {
    background: rgba(55, 60, 79, 0.5);
}
.features-wrapper .practice-account {
    background: rgb(55, 60, 79); 
}
.practice-account .amount,
.btn.watch-video {
    color: #f07001;
}
.practice-account h6 span {
    display: inline-block;
}
.margin-trading {
    border: 1px solid #555;
    margin-top: 10px;
}
.margin-trading .value {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.8);
    width: 90%;
}
.btn.watch-video {
    padding: 0;
    padding-top: 5px;
    font-size: 12px;
}
.btn-outline-success.deposit {
    font-size: 20px;
    border: 1px solid rgb(42, 157, 62);
    padding: 10px 25px;
}
.btn-outline-success.deposit:hover {
    background: rgb(42, 157, 62);
}

